'use client';
import { useState } from 'react';
import { Textarea } from '@/components/ui/textarea';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Resultado } from '@/interfaces/resultado';
import { Quiz } from '@/interfaces/quiz';

export default function GeradorExplicacoes() {
  const [tema, setTema] = useState('');
  const [resultado, setResultado] = useState<Resultado | undefined>(undefined);
  const [loading, setLoading] = useState(false);
  const [respondido, setRespondido] = useState<boolean[]>(Array(resultado?.quiz.length).fill(false));
  const [respostas, setRespostas] = useState<number[]>(Array(resultado?.quiz.length).fill(-1));

  const selecionarAlternativa = (quizIndex: number, alternativaIndex: number) => {
    const novas = [...respostas];
    novas[quizIndex] = alternativaIndex;
    setRespostas(novas);
  };

  async function gerarExplicacao() {
    setLoading(true);
    try {
      const response = await fetch("http://localhost:3333/api/gerar-explicacao", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ tema }),
      });

      if (!response.ok) throw new Error("Erro na resposta da API");

      const json = await response.json();

      // Aqui convertemos para o tipo Resultado
      const resultadoAPI = json as Resultado;

      // Validação simples (opcional)
      if (
        resultadoAPI.explicacao &&
        resultadoAPI.analogia &&
        resultadoAPI.exemplo &&
        Array.isArray(resultadoAPI.quiz)
      ) {

        setResultado(resultadoAPI)
      }
      setLoading(false);

    } catch (err) {
      console.error("Erro ao gerar explicação");
      setLoading(false);

    }
  }

  const validarResposta = (quizIndex: number) => {
    const novos = [...respondido];
    novos[quizIndex] = true;
    setRespondido(novos);
  };
  return (
    <div className="max-w-2xl mx-auto p-4 space-y-6">
      <h1 className="text-2xl font-bold text-center">📚 Ta Ligado?</h1>
      <h2 className="text-lg font-semibold text-center">Descubra sobre um tema que quer em poucos segundos 💡</h2>
      <p className="text-center text-muted-foreground">Digite um tema e receba uma explicação simples com exemplos e quiz.</p>

      <Textarea
        placeholder="Ex: Revolução Francesa, Mitose, Função do 1º grau..."
        value={tema}
        onChange={(e) => setTema(e.target.value)}
        className="min-h-[100px]"
      />
      <Button onClick={gerarExplicacao} disabled={loading}>
        {loading ? 'Gerando...' : 'Gerar Explicação'}
      </Button>

      {resultado && (
        <Card>
          <CardContent className="space-y-4 p-4">
            <div>
              <h2 className="font-semibold">✅ Explicação:</h2>
              <p>{resultado.explicacao}</p>
            </div>
            <div>
              <h2 className="font-semibold">🎓 Analogia:</h2>
              <p>{resultado.analogia}</p>
            </div>
            <div>
              <h2 className="font-semibold">🧪 Exemplo:</h2>
              <p>{resultado.exemplo}</p>
            </div>
            <div>
              <h2 className="font-semibold">🧠 Quiz:</h2>
              {resultado.quiz.map((q, quizIndex) => (
                <div key={quizIndex} className="mb-6 border p-4 rounded-xl shadow-sm">
                  <p className="font-semibold mb-2">{q.pergunta}</p>

                  <div className="flex flex-col gap-2">
                    {q.alternativas.map((alt, altIndex) => {
                      const selecionada = respostas[quizIndex] === altIndex;
                      const certa = q.resposta === altIndex;
                      const foiRespondido = respondido[quizIndex];

                      let bg = "bg-gray-100";
                      if (foiRespondido) {
                        if (selecionada && certa) bg = "bg-green-200";
                        else if (selecionada && !certa) bg = "bg-red-200";
                        else if (certa) bg = "bg-green-100";
                      } else if (selecionada) {
                        bg = "bg-blue-200";
                      }

                      return (
                        <button
                          key={altIndex}
                          onClick={() => selecionarAlternativa(quizIndex, altIndex)}
                          className={`${bg} text-left px-4 py-2 rounded-md border hover:brightness-95`}
                          disabled={foiRespondido}
                        >
                          <span className="font-bold">{String.fromCharCode(65 + altIndex)}.</span> {alt}
                        </button>
                      );
                    })}
                  </div>

                  {!respondido[quizIndex] && (
                    <button
                      onClick={() => validarResposta(quizIndex)}
                      className="mt-3 bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
                      disabled={respostas[quizIndex] === -1}
                    >
                      Responder
                    </button>
                  )}

                  {respondido[quizIndex] && (
                    <p className="mt-2 text-sm">
                      {respostas[quizIndex] === q.resposta
                        ? "✅ Resposta correta!"
                        : `❌ Resposta incorreta. A correta era ${String.fromCharCode(65 + q.resposta)}.`}
                    </p>
                  )}
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
